import React from 'react';
import ReactDOM from 'react-dom';
import Metronome from './Metronome';
import './App.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";

function SettingsButton(props) {
  let classes = "button-theme-toggle hamburger";
  if (props.isMenuOpen) {
    classes = "button-theme-toggle hamburger open";
  }

  return (
      <button className={classes} name="menu-toggle" onClick={props.onClick}>
        <span></span>
        <span></span>
        <span></span>
      </button>
  );
}

class Navbar extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="navbar">
        <div className="logo">
          Zen Metronome
        </div>
          <SettingsButton onClick={this.props.onClick} isMenuOpen={this.props.isMenuOpen} />
      </div>
    );
  }
}

class MobileMenu extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    let classes = "mobile-menu";
    if (this.props.isMenuOpen) {
      classes += " mobile-menu-open";
    }

    return(
      <div className={classes}>
        <div className="menu-row">
          <button className="color color-purple" value="#d7d|#f7f" onClick={this.props.onClick}>
            {this.props.color === "#d7d" ? <FontAwesomeIcon icon={faCheck} size="xs"/> : null}
          </button>
          <button className="color color-blue" value="#77d|#77f" onClick={this.props.onClick}>
            {this.props.color === "#77d" ? <FontAwesomeIcon icon={faCheck} size="xs"/> : null}
          </button>
          <button className="color color-green" value="#7d7|#7f7" onClick={this.props.onClick}>
            {this.props.color === "#7d7" ? <FontAwesomeIcon icon={faCheck} size="xs"/> : null}
          </button>
          <button className="color color-grey" value="#777|#999" onClick={this.props.onClick}>
            {this.props.color === "#777" ? <FontAwesomeIcon icon={faCheck} size="xs"/> : null}
          </button>
        </div>
        <div className="menu-row">
          <a href="https://aviolin.github.io/metronome" target="_blank">View the source on Github</a>
        </div>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      color: "#d7d",
      colorHighlight: "#f7f",
      isMenuOpen: false
    };

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    if (e.target.name === "menu-toggle") {
      this.setState(state => ({
        isMenuOpen: !state.isMenuOpen
      }));
      return;
    }
    
    this.updateColors(e.target.value);
  }

  updateColors(colorString) {
    let colors = colorString.split("|");

    let root = document.documentElement;
    root.style.setProperty('--accentcolor', colors[0]);
    root.style.setProperty('--accentcolorhighlight', colors[1]);

    this.setState(state => ({
      color: colors[0],
      colorHighlight: colors[1],
      isMenuOpen: !state.isMenuOpen
    }));
  }

  render() {
    return(
      <div>
        <Navbar onClick={this.handleClick} isMenuOpen={this.state.isMenuOpen}/>
        <MobileMenu isMenuOpen={this.state.isMenuOpen} onClick={this.handleClick} color={this.state.color} />
        <Metronome color={this.state.color} colorHighlight={this.state.colorHighlight} />
      </div>  
    ); 
  }
}

// ========================================

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);