import React from 'react';
import Animation from './Animation';
import TempoInputs from './TempoInputs';
import Spinner from './Spinner';
import worker from 'workerize-loader!./worker' // eslint-disable-line import/no-webpack-loader-syntax
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import { faStop } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Note {
  constructor(time = 0, beat = 0, click = 0) {
    this.time = time;
    this.beat = beat;
    this.click = click;
  }
}

function PlayButton(props) {
  const playElement = <FontAwesomeIcon icon={faPlay} />;
  const stopElement = <FontAwesomeIcon icon={faStop} />;
  return (
    <button className="button-play" onClick={props.onClick} name="play-button">
      {props.value === "Start" ? playElement : stopElement}
    </button>
  );
}

/***
 * 
 * MAIN METRONOME COMPONENT
 * 
 ***/

export default class Metronome extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {
        noteLength: .05,
        lookahead: 25,
        scheduleAheadTime: .1,

        tempo: 120,
        maxTempo: 240,
        minTempo: 30,

        beatsPerBar: 4,
        maxBeats: 12,
        minBeats: 1,
        nextBeat: 0,
        nextBeatTime: 0,

        clicksPerBeat: 1,
        maxClicks: 12,
        minClicks: 1,
        nextClick: 0,
        nextClickTime: 0,

        isPlaying: false,
        audioUnlocked: false,
        noteQueue: [],      
      };
      
      this.handleChange = this.handleChange.bind(this);
      this.handleClick = this.handleClick.bind(this);
      this.handleTick = this.handleTick.bind(this);
  
      var AudioContext = window.AudioContext
                      || window.webkitAudioContext
                      || false; 
      this.audioContext = new AudioContext({
        latencyHint: .1
      });
     
      var buffer = this.audioContext.createBuffer(1, 1, 22050);
      var node = this.audioContext.createBufferSource();
      node.buffer = buffer;
      node.start(this.audioContext.currentTime);
  
      this.timerWorker = worker();
      this.timerWorker.onmessage = (e) => {
        if (e.data === "tick") {
          this.handleTick();
        }
        else
          console.log("Worker: " + e.data);
      };
      this.timerWorker.postMessage({"interval":this.state.lookahead});
    }
  
    scheduleNote(note) {
      var osc = this.audioContext.createOscillator();
      var gainNode = this.audioContext.createGain();
      osc.connect(gainNode);
      gainNode.connect(this.audioContext.destination);

      if (note.beat !== 0) {
        osc.frequency.value = 440.0;
      } else {
        osc.frequency.value = 880.0;
      }
      if (note.click !== 0) {
        osc.frequency.value = 330.0;
      }
  
      osc.start(this.state.nextClickTime);
      gainNode.gain.setTargetAtTime(0, this.state.nextClickTime + this.state.noteLength, 0.015);
      osc.stop(this.state.nextClickTime + this.state.noteLength*2);
    }
  
    handleTick() {
      while (this.state.nextClickTime < this.audioContext.currentTime + this.state.scheduleAheadTime) {
  
        let nextBeat = this.state.nextBeat;
        let nextClick = this.state.nextClick;
        let secondsPerBeat = 60.0 / this.state.tempo;
        let nextBeatTime = this.state.nextBeatTime;
      
        let nextNote = new Note(this.state.nextClickTime, nextBeat, nextClick);  
        if (nextNote.click === 0) {
          nextBeatTime = nextNote.time + secondsPerBeat;
        }
        this.scheduleNote(nextNote);
  
        if (this.state.clicksPerBeat > 1) {
          nextClick++;
          if (nextClick >= this.state.clicksPerBeat) {
            nextClick = 0;
            nextBeat++;
            if (nextBeat >= this.state.beatsPerBar) {
              nextBeat = 0;
            }
          }
        } else {
          nextBeat++;
          nextClick = 0;
          if (nextBeat >= this.state.beatsPerBar) {
            nextBeat = 0;
          }
        }
  
        this.setState(state => {
          const queue = state.noteQueue.concat(nextNote);
          return {
            noteQueue: queue,
            nextClickTime: state.nextClickTime + 1/state.clicksPerBeat * secondsPerBeat,
            nextBeatTime: nextBeatTime,
            nextBeat: nextBeat,
            nextClick: nextClick
          };
        });
      }
    }
  
    fadeAnimation(id) {
      document.getElementById(id).classList.remove("animation-fade");
      void document.getElementById(id).offsetWidth;
      document.getElementById(id).classList.add("animation-fade");
    }
    
    handleClick(e) {
      let nextTempo, nextBeats, nextClicks = 0;
      switch (e.target.name) {
        case "increment-tempo":
          this.fadeAnimation("tempo-label");
          nextTempo = parseInt(this.state.tempo)+1;
          this.setState(state => ({tempo: nextTempo > state.maxTempo ? state.tempo : nextTempo}));
          return;
        case "decrement-tempo":
          this.fadeAnimation("tempo-label");
          nextTempo = parseInt(this.state.tempo)-1;
          this.setState(state => ({tempo: nextTempo < state.minTempo ? state.tempo : nextTempo}));
          return;
        case "increment-beats":
          this.fadeAnimation("beats");
          nextBeats = parseInt(this.state.beatsPerBar)+1;
          this.setState(state => ({beatsPerBar: nextBeats > state.maxBeats ? state.beatsPerBar : nextBeats}));
          return;
        case "decrement-beats":
          this.fadeAnimation("beats");
          nextBeats = parseInt(this.state.beatsPerBar)-1;
          this.setState(state => ({beatsPerBar: nextBeats < state.minBeats ? state.beatsPerBar : nextBeats}));
          return;
        case "increment-clicks":
          this.fadeAnimation("clicks");
          nextClicks = parseInt(this.state.clicksPerBeat)+1;
          this.setState(state => ({clicksPerBeat: nextClicks > state.maxClicks ? state.clicksPerBeat : nextClicks}));
          return;
        case "decrement-clicks":
          this.fadeAnimation("clicks");
          nextClicks = parseInt(this.state.clicksPerBeat)-1;
          this.setState(state => ({clicksPerBeat: nextClicks < state.minClicks ? state.clicksPerBeat : nextClicks}));
          return;
        default:
          break;
      }

      this.timerWorker.postMessage(this.state.isPlaying ? "stop" : "start");

      if (this.state.isPlaying) {
        this.audioContext.close();

        this.setState(state => ({
          isPlaying: !state.isPlaying,
          audioUnlocked: false,
          noteQueue: [],
          nextClickTime: 0,
          nextBeat: 0,
          nextClick: 0
        }));
      }
      else {
        let AudioContext = window.AudioContext
                         || window.webkitAudioContext
                         || false; 
        this.audioContext = new AudioContext({
          latencyHint: .1
        });

        if (!this.state.audioUnlocked) {
          var buffer = this.audioContext.createBuffer(1, 1, 22050);
          var node = this.audioContext.createBufferSource();
          node.buffer = buffer;
          node.start(this.audioContext.currentTime);
        }

        this.setState(state => ({
          isPlaying: !state.isPlaying,
          audioUnlocked: true
        }));
      }
    }
    
    handleChange(e) {
      this.fadeAnimation("tempo-label");
      this.setState({tempo: e.target.value});
    }
  
    render() {
      return (
        <div className="metronome">
          <Animation 
              tempo={this.state.tempo} 
              color={this.props.color} 
              nbt={this.state.nextBeatTime} 
              isplaying={this.state.isPlaying} 
              queue={this.state.noteQueue} 
              audiocontext={this.audioContext} />
          <div className="inputs">
          <PlayButton value={this.state.isPlaying ? "Stop" : "Start"} onClick={this.handleClick} />
            <TempoInputs 
                onChange={this.handleChange} 
                onClick={this.handleClick} 
                tempo={this.state.tempo} 
                maxTempo={this.state.maxTempo} 
                minTempo={this.state.minTempo} />
            <div className="input-row">
              <div className="spinner-inputs">
                <Spinner label="Beats per bar" id="beats" onClick={this.handleClick} value={this.state.beatsPerBar} />
                <Spinner label="Clicks per beat" id="clicks" onClick={this.handleClick} value={this.state.clicksPerBeat} />
              </div>
            </div>
          </div>
        </div>
      );
    }
  }